import Vue from 'vue'
import VueRouter from 'vue-router'
import login from './page/login.vue'
import register from './page/register.vue'

import index from './page/index.vue'
import goodsDetail from './page/index/goodsDetail.vue'
import hot from './page/index/hot.vue'

import shopCart from './page/shopCart.vue'

import my from './page/my.vue'
import reCharge from './page/my/reCharge/reCharge.vue' //
import reChargeSuccess from './page/my/reCharge/reChargeSuccess.vue' //
import takeGoods from './page/my/myOrder/takeGoods.vue'
import chooseAddress from './page/my/myOrder/chooseAddress.vue'
import myOrder from './page/my/myOrder/myOrder.vue'

import address from './page/my/address/address.vue' //
import newAddress from './page/my/address/newAddress.vue' //
import editAddress from './page/my/address/editAddress.vue'

import bankCard from './page/my/bankCard/bankCard.vue' //
import newBankCard from './page/my/bankCard/newBankCard.vue'
import balanceDetail from './page/my/balanceDetail.vue' //
import withdraw from './page/my/withdraw/withdraw.vue' //
import withdrawSubmit from './page/my/withdraw/withdrawSubmit.vue' //
import chooseBankCard from './page/my/withdraw/chooseBankCard.vue' 


Vue.use(VueRouter)
const routes = [
  {
    name:'register',
    path: '/register',             // 
    component: register
  },
  {
    name:'login',
    path: '/login',             // 
    component: login
  },
  {
    path: '/',                 // 
    component: index
  },
  {
    name: 'index',
    path: '/index',             // 
    component: index
  },
  {
    name:'goodsDetail',
    path: '/goodsDetail/:goodsDetail',             // 
    component: goodsDetail
  },
  {
    name:'hot',
    path: '/hot/:keyWord',             // 
    component: hot
  },
  {
    name:'shopCart',
    path: '/shopCart',             // 
    component: shopCart
  },
  {
    name:'my',
    path: '/my',             // 
    component: my
  },
  {
    name:'myOrder',
    path: '/myOrder/:status',             // 
    component: myOrder
  },
  {
    path: '/reCharge',             //  
    component: reCharge
  },
  {
    name: 'reChargeSuccess',
    path: '/reChargeSuccess',             //  
    component: reChargeSuccess
  },
  {
    path: '/address',             // 
    component: address
  },
  {
    name:'takeGoods',
    path: '/takeGoods/:goodsDetail',             //  
    component: takeGoods
  },
  {
    name:'chooseAddress',
    path: '/chooseAddress',             //  
    component: chooseAddress
  },
  {
    name:'newAddress',
    path: '/newAddress',             //  
    component: newAddress
  },
  {
    name:'editAddress',
    path: '/editAddress/:editData',             //  bianji
    component: editAddress
  },
  {
    path: '/bankCard',             // 
    component: bankCard
  },
  {
    path: '/newBankCard',             //  
    component: newBankCard
  },
  {
    path: '/balanceDetail',             // 
    component: balanceDetail
  },
  {
    name:'withdraw',
    path: '/withdraw',             // 
    component: withdraw
  },
  {
    name:'withdrawSubmit',
    path:'/withdrawSubmit/:memberPayoffInfo',           //
    component:withdrawSubmit
  },
  {
    name:'chooseBankCard',
    path:'/chooseBankCard',
    component:chooseBankCard
  }
]
var router = new VueRouter({
  routes
})
export default router



